var projectNamePathIndex = location.pathname.indexOf('.html');
var base = process.env.BASE_URL;

if (projectNamePathIndex >= 0) {
  base = "/".concat(location.pathname.slice(1, projectNamePathIndex), ".html");
} // console.log(base)


var router = store.state.tld === "github.io" ? new Router({
  base: base,
  routes: routes
}) : new Router({
  mode: 'history',
  base: base,
  routes: routes
}); // 路由守卫

router.beforeEach(function (to, from, next) {
  /**设置每个页面的title */
  if (to.meta.title) {
    document.title = to.meta.title;
  }

  var isLogin = store.state.token ? true : false;
  /**页面路由守卫：判断：是否需要登录访问 */

  if (to.meta.requireAuth === false) {
    // 不需要登录访问
    next();
  } else {
    // 需要登录访问
    if (isLogin) {
      next();
    } else {
      Toast.$create({
        type: 'txt',
        txt: '登录状态失效，请重新登录'
      }).show();
      store.commit('resetAll');
      next({
        path: '/user/login',
        query: {
          redirect: to.fullPath
        }
      });
    }
  }
});
export default router;